body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "quintesential";
  src: url(/undangan-app/static/media/Quintessential-Regular.d4431dc1.ttf);
}
@font-face {
  font-family: "charm";
  src: url(/undangan-app/static/media/Charm-Regular.ca8b43c8.ttf);
}
body{
background-color: #FFFDF8;
font-family: charm;
font-size: small;
}
.App-header{
  text-align: center;
}
.awalan{
  display: inline-block;
  margin-top: 20%;
  display: flexbox;
}
.img{
display: inline-block;
width: 67px;
height: 50px;
background-color: #fff;
}
.amplop3{
  width: 67px;
  height: 50px;
  border-radius: 5px;
  margin: 0 auto;
}
.marp{
  margin-top: -8px;
  z-index: 1;
}
#image{
width: 67px;
height: 50px;
}
center{
background-color: #FFFDF8;
padding: 5px;
padding-left: 5px;
padding-right: 5px;
border-radius: 10px;
margin: 20px;
box-shadow: -1px 0px 3px 2px #696a4c;
}
h1{
color: #696a4c;
}
h2{
color: #696a4c;
}
h3{
color: #696a4c;
}
h4{
color: #696a4c;
}
p{
color: #696a4c;
}
.sampul{
display: inline-block;
background-image: url(/undangan-app/static/media/undangan.e699322e.png);
background-size: contain;
padding-top: 10px;
padding-bottom: 10px;
}
.img-sampul{
height: 150px;
width: 160px;
}
.mempelai{
border-top: #696a4c double;
border-bottom: #696a4c double;
background-image: url(/undangan-app/static/media/sampul2.ab643f4a.png);
background-size: contain;
}
.akad{
background-image: url(/undangan-app/static/media/akad1.addcba71.png);
background-size: contain;
padding-top: 18px;
padding-bottom: 18px;
border-bottom: #383a28 5px double;
border: 20px solid transparent;
padding: 15px; 
border-image: url(/undangan-app/static/media/border.f3995bdb.png) 55 round;
}
.divcopi{
  color: #383a28;
  background-color: #FFF;
  border: 2px solid #383a28;
  display: inline-block;
  padding: 3px 5px 3px 5px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
}
.button{
  color: #FFF;
  background-color: #696a4c;
  margin-top: 5px;
  display: inline-block;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 0px 0px 5px 1px rgb(33, 45, 34);
  margin: 10px;
}
.kado{
  border-top: #383a28 3px double;
  padding-top: 25px;
}
.kado .salinNomerRek{
  color: #fff;
  background-color: #696a4c;
  display: inline-block;
  padding: 5px;
  border-radius: 7px;
}
.kado .salinAlamat{
  color: #fff;
  background-color: #696a4c;
  display: inline-block;
  padding: 5px;
  border-radius: 7px;
}
.kado .amplop img{
  margin-top: 20px;
}
.imgRek{
  border: 2px  solid #696a4c;
  border-radius: 5px;
  margin-bottom: -10px;
}
.kado .amplop h2{
  margin-top: -5px;
}
.kadoDua{
  margin-top: 50px;
  background-color: #696a4c;
  margin-bottom: 50px;
  padding-bottom: 10px;
  margin-left: -5px;
  margin-right: -5px;
  border-radius: 5px;
}
.kadoDua h2{
  color: #FFFDF8;
  padding: 5px;
}
.imgkadodua{
  display: inline-block;
  width: 80px;
  height: 70px;
  border-radius: 10px;
  background-color: #FFFDF8;
  margin-bottom: 5px;
}
.kadoDua img{
  margin-top: -10px;
  border-radius: 5px;
}
.protokol{
display: inline-block;
border: 2px solid #696a4c;
background-color: #696a4c;
border-radius: 20px;
}
.protokol h2{
color: #FFFDF8;
margin: 3px;
}
.listProt{
  width: 350px;
  height: 200px;
  display: inline-block;
  background-color: #FFFDF8;
  margin-bottom: 10px;
  border-radius: 10px;
}
.listProt img{
  margin: 2px;
  border-radius: 5px;
}
.penutup{
background-image: url(/undangan-app/static/media/penutup.c3b7adce.png);
background-size: contain;
border-top: 4px #696a4c double;
margin-top: 10px;
padding-top: 10px;
}
.pasangan{
  background-image: url(/undangan-app/static/media/pasangan1.33b677a4.jpeg);
  background-size: cover;
  width: 360px;
  height: 200px;
  border-radius: 10px;
  border: 1px solid #383a28;
  box-shadow: 0px 0px 5px 1px rgb(33, 45, 34);
  color: #FFF;
}
.navbar{
  position: fixed;
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: 25px;
  background-image: url(/undangan-app/static/media/nav4.71ffdf39.png);
  background-size: contain;
  border-top: 3px solid #383a28;
  z-index: 9999;
  padding-bottom: 5px;
}
.nav{
  background-color: #696a4c;
  opacity: 0.9;
  width: 280px;
  height: 25px;
  margin: 0 auto;
  border-left: 2px solid #383a28;
  border-right: 2px solid #383a28;
  border-bottom: 2px solid #383a28;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  top: 0px;
  bottom: 0px;
  padding-top: -1px;
  padding-bottom: 3px;
  box-shadow: -1px 0px 3px 2px rgb(33, 45, 34);
}
.nav p{
  color: #fff;
  display: inline;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 18px;
}
a{
color: #fff;
text-decoration: none;
}
marquee{
  width: 200px;
}
